.item-catalog-teaser3 {
  display: grid;
  grid-template: auto/100px auto;
  grid-auto-flow: column;
  outline: 1px solid #D6D6D6;
  background: #fff;
  grid-gap: 20px;
}

.item-catalog-teaser3:hover .item__title h3 {
  color: #AE2121;
}

.item-catalog-teaser3 .item__title h3 {
  font-size: 15px;
  margin: 0 0 7px 0;
  text-align: left;
  line-height: 20px;
  font-weight: 700;
}

.item-catalog-teaser3 .item__content {
  padding: 50px 18px 50px 0;
  position: initial;
  background: #fff;
}

.item-catalog-teaser3 .item__image {
  display: flex;
  background: #FFF;
}

.item-catalog-teaser3 .item__image .item__link {
  margin: 58px 0 auto 18px;
  border-bottom: 0;
}

.item-catalog-teaser3 .item__image img {
  width: 100%;
  object-fit: contain;
  /*! margin: 0 auto; */
}

.item-catalog-teaser3 .item__desc {
  font-weight: 500;
  line-height: 26px;
}

.item-catalog-teaser3 .item__desc > *:first-child {
  margin-top: 0;
}

.item-catalog-teaser3 .item__desc ul,
.item-catalog-teaser3 .item__desc p {
  margin: 10px 0;
}

.item-catalog-teaser3 .item__desc ul {
  padding-left: 0;
}

.item-catalog-teaser3 .item__desc li:before {
  display: none;
}

.item-catalog-teaser3 .item__desc > *:last-child {
  margin-bottom: 0;
}

.item-catalog-teaser3 .item__price {
  margin-top: 7px;
  line-height: 32px;
  font-weight: 500;
}

.item-catalog-teaser3 .item__price .item__price-value {
  font-size: 30px;
  font-weight: 700;
  color: #333333;
  display: inline;
  margin-left: 0.2ch;
}

.item-catalog-teaser3 .item__label {
  position: absolute;
  top: 11px;
  left: 0;
}

.item-catalog-teaser3 .item__to-favorite {
  position: absolute;
  top: 15px;
  right: 15px;
}

@media only screen and (min-width: 1024px) {
  .item-catalog-teaser3 {
    grid-template: auto/190px auto;
  }

  .item-catalog-teaser3 .item__title h3 {
    font-size: 16px;
    line-height: 22px;
  }

  .item-catalog-teaser3 .item__content {
    padding: 32px 40px 23px 0;
  }

  .item-catalog-teaser3 .item__image {
    background: #F8F8F8;
  }

  .item-catalog-teaser3 .item__image .item__link {
    margin: auto;
    padding: 59px 20px 20px;
  }

  .item-catalog-teaser3 .item__desc {
    font-size: 13px;
  }
}

@media only screen and (min-width: 1280px) {
  .item-catalog-teaser3 {
    grid-template: auto/1fr 1.5fr;
  }

  .item-catalog-teaser3 .item__title h3 {
    font-size: 20px;
    line-height: 28px;
  }

  .item-catalog-teaser3 .item__content {
    padding: 32px 40px 32px 0;
  }

  .item-catalog-teaser3 .item__desc {
    font-size: 15px;
    line-height: 32px;
  }
}